import React, { useEffect } from "react";
const { kakao } = window;
const Map = () => {
  useEffect(() => {
    const container = document.getElementById("map"); //   DOM 
    const options = {
      center: new kakao.maps.LatLng(37.46877499136315, 126.88754810027754), // .
      level: 2,
    };

    const map = new kakao.maps.Map(container, options); //    
    //   
    let markerPosition = new kakao.maps.LatLng(
      37.46877499136315,
      126.88754810027754
    );
    let marker = new kakao.maps.Marker({
      position: markerPosition,
    });
    marker.setMap(map);

    var iwContent =
        '<div style="padding:5px;"> <br><a href="http://kko.to/uAtNkd797a" style="color:blue" target="_blank"></a> <a href="https://map.kakao.com/link/to/ 10,37.46877499136315,126.88754810027754" style="color:blue" target="_blank"></a></div>', //    HTML  document element 
      iwPosition = new kakao.maps.LatLng(37.46877499136315, 126.88754810027754); //  

    //  
    var infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    //    .   marker     
    infowindow.open(map, marker);
  }, []);

  return (
    <>
      <div id="map" style={{ width: "100vh", height: "60vh" }}></div>
    </>
  );
};

export default Map;
